* {
  transition: 0.2s;
}

Link {
  text-decoration: none;
}

.home-container {
  position: fixed;
  overflow: auto;
  width: 100vw;
  height: 100vh;
}

.home-container .main {
  overflow: hidden;
  margin-top: 70px;
  display: flex;
}

.home-container .main .menu {
  z-index: 1000;
  position: fixed;
  padding: 2rem 0 0 1rem;
  background: white;
  width: 280px;
  height: calc(100vh - 70px);
  box-shadow: 2px 10px 10px rgba(0, 0, 0, 0.05);
}

.home-container .main .menu ul {
  height: 100%;
  list-style: none;
}

.home-container .main .menu ul .selected {
  background: var(--primaryLight);
  color: var(--primaryColor);
}

.home-container .main .menu ul li {
  font-size: 1rem;
  font-weight: 500;
  color: black;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  border-radius: 10px 0 0 10px;
  background: white;
  padding: 1rem;
  margin-bottom: 1rem;
}

.home-container .main .menu ul li:hover {
  background: var(--primaryLight);
}

.home-container .main .menu ul li:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.content {
  margin-left: 280px;
  padding: 2rem;
}

@media (max-width: 768px) {
  .home-container .main .menu {
    width: 5rem;
  }
  .home-container .main .menu li {
    display: flex;
  }
  .home-container .main .menu li text {
    opacity: 0;
  }
  .content {
    margin-left: 5rem;
    padding: 2rem;
  }
}
