.content-body {
  min-width: 500px;
  width: calc(100vw - (280px + 4rem));
  display: flex;
  flex-direction: column;
  position: relative;
}

.content-body .content-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content-body .content-title p {
  font-size: 2rem;
  font-weight: 500;
}

input,
select {
  background: var(--primaryLight);
  padding: 0.7rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  border-style: solid;
  border-color: transparent;
  outline: none;
  color: var(--primaryColor);
}

input:focus {
  border-color: var(--primaryColor);
}

button {
  background-color: var(--primaryColor);
  color: white;
  padding: 0.7rem 2rem;
  border-radius: 10px;
  border: 0;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  box-shadow: 0 4px 8px var(--primaryLight);
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.disabled {
  background: #c8c8c8;
  pointer-events: none;
}

table {
  font-size: 1rem;
  font-weight: 500;
  padding-top: 2rem;
  width: 100%;
}

table th {
  background: var(--primaryColor);
  color: white;
}

table tr {
  height: 4rem;
  text-align: center;
}

table tr img {
  padding: 0.5rem;
  width: 4rem;
}

table tr:nth-child(even) {
  background: var(--primaryLight);
}

table td {
  max-width: 300px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.order-details-table {
  font-size: 1rem;
  font-weight: 500;
  padding-top: 0;
  width: 50%;
}

.order-details-table th {
  background: white;
  color: white;
}

.order-details-table tr {
  height: 4rem;
  text-align: start;
}

.order-details-table tr img {
  padding: 0.5rem;
  width: 4rem;
}

.order-details-table tr:nth-child(even) {
  background: white;
}

.order-details-table td {
  max-width: 300px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.form {
  width: 500px;
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
}

.form p {
  font-size: 1.2rem;
  font-weight: 500;
}

.form input,
.form button,
.form select {
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
}

.modalContent {
  padding: 0.5rem;
}

.modalContent .modalTitle {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.modalContent .modalBody {
  font-size: 1rem;
}

.modalContent .modalButtonsRow {
  display: flex;
  width: 100%;
  margin-top: 2rem;
}

.modalContent .modalButtonsRow button {
  width: 100%;
}

.modalContent .modalButtonsRow button:nth-child(1) {
  color: var(--primaryColor);
  background: white;
  margin-right: 1rem;
}

a {
  text-decoration: none;
  color: var(--primaryColor);
}

a:hover {
  text-decoration: underline;
}

.dismissible {
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 20%;
  transition: 0.4s;
}

.dismissible img {
  width: 100%;
}

.dismissible .icon {
  position: absolute;
  opacity: 0;
}

.dismissible:hover img {
  opacity: 0;
}

.dismissible:hover .icon {
  opacity: 1;
}

.card {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  justify-content: space-between;
  margin-right: 2rem;
  margin-bottom: 2rem;
  padding: 2rem;
  border-radius: 1rem;
  background-color: var(--primaryColor);
  color: white;
  width: 350px;
  height: 180px;
  font-size: 1.4rem;
  font-weight: 600;
}

.card span {
  font-size: 3rem;
}

.card:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.card:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.yellow-card {
  background: #ffe8b2;
  color: #f0a800;
}

.yellow-card:hover {
  box-shadow: 0 0 20px rgba(240, 168, 0, 0.1);
}

.blue-card {
  background: #d3e2ff;
  color: #638fe2;
}

.blue-card:hover {
  box-shadow: 0 0 20px rgba(99, 143, 226, 0.1);
}

.green-card {
  background: #d1f9f5;
  color: #28c5ae;
}

.green-card:hover {
  box-shadow: 0 0 20px rgba(40, 197, 174, 0.1);
}

.red-card {
  background: #facaca;
  color: #ff6e6e;
}

.red-card:hover {
  box-shadow: 0 0 20px rgba(255, 110, 110, 0.1);
}

.orange-card {
  background: #ffddbf;
  color: #f18944;
}

.orange-card:hover {
  box-shadow: 0 0 20px rgba(241, 137, 68, 0.1);
}

.pink-card {
  background: #ffcee3;
  color: #ff4a77;
}

.pink-card:hover {
  box-shadow: 0 0 20px rgba(255, 74, 119, 0.1);
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .row {
    flex-direction: column;
  }
  .content-title {
    justify-content: left;
  }
  .form {
    width: calc(100% - 10rem);
  }
}
